// --c-gateway : The Gateway
// --c-newsroom : The Newsroom
// --c-stack : The Stack
// --c-alt : The Alternate  





// BEGIN THEME STYLES

.c-featured-stories:first-of-type {
  width: 100%;
  padding-bottom: .5em;
  background: linear-gradient(180deg, #005587 0%, #2774AE 100%);
  margin: var(--spacing-large) 0;
}

[data-layout*="c-gateway"] {
  display: grid;
  gap: var(--grid-column-gap);
  margin: var(--spacing-large) var(--spacing-default);
  max-width: var(--max-width);
  transform: translateY(-24px);
}

[data-layout*="c-gateway"] article {
  margin-bottom: var(--spacing-medium);
}

@media (min-width: 48em) {
  [data-layout*="c-gateway"] {
    display: grid;
    gap: var(--grid-column-gap);
    grid-template-columns: 1fr 1fr;
    grid-template-areas:
      "🐻 🐻"
      "🐶 🐱";
    margin: var(--spacing-large);
  }

  [data-layout*="c-gateway"] > :nth-child(1) {
    grid-area: 🐻;
  }

  [data-layout*="c-gateway"] > :nth-child(2) {
    grid-area: 🐶;
  }
  
  [data-layout*="c-gateway"] > :nth-child(3) {
    grid-area: 🐱;
  }
  
  [data-layout*="c-gateway"] > :first-child h3 {
    padding: 1em 1em .8em;
    width: 66%;
  }
  
  [data-layout*="c-gateway"] > :not(:first-child) h3 {
    width: 80%;
    padding: 0.75em 1em .5em;
    font-size: 1em;
  }

 
} 

@media (min-width: 60em) {
  [data-layout*="c-gateway"] {
    display: grid;
    gap: var(--gap);
    grid-template-columns: 2.5fr 1fr;
    grid-template-areas:
      "🐻 🐶"
      "🐻 🐱";
    margin: var(--spacing-large) auto;
  }

}


[data-layout*="c-gateway"] > article {
  display: grid;
  grid-template-columns: 1fr;
  margin: 0;

  > * {
    grid-row: 1;
    grid-column: 1 / -1;
  }

  > figure {
    z-index: 1;
  }

  > div {
    z-index: 2;
    height: fit-content;
    align-self: end;
  }

  h3 {
    position: relative;
    background-color: var(--ucla-white);
    color: var(--ucla-blue);
  }

  h3::before {
    content: '';
    display: block;
    position: absolute;
    top: -2px;
    left: 16px;
    width: 64px;
    height: 4px;
    background: #FFD100;
    margin-bottom: 24px;
  }

  h3:hover {
    background-color: var(--ucla-blue);
    color: var(--ucla-white);
  }

  .p-summary,
  .dt-published {
    display: none;
  }

  img {
    width: 100%;
    object-fit: cover;
  }


  a {
    display: block;
    color: inherit;
    background-color: inherit;
    text-decoration: none;
  }

}

