@media (min-width: 60em) {
  [data-layout*="c-newsroom"] {
    display: grid;
    gap: var(--gap);
    grid-template-columns: 1fr 1fr 1fr;
    grid-template-areas:
      "🐻 🐻 🐻"
      "🏀 ⚽️ 🏈";
    margin: var(--spacing-large) auto;
  }
}

[data-layout*="c-newsroom"] > :nth-child(1) {
  grid-area: 🐻;
}

[data-layout*="c-newsroom"] > :nth-child(2) {
  grid-area: 🏀;
}

[data-layout*="c-newsroom"] > :nth-child(3) {
  grid-area: ⚽️;
}

[data-layout*="c-newsroom"] > :nth-child(4) {
  grid-area: 🏈;
}
  

[data-layout*="c-newsroom"] > :first-child h3 {
  padding: 1em 1em .8em;
  width: 66%;
}


[data-layout*="c-newsroom"] {
  display: grid;
  gap: var(--grid-column-gap);
  margin: var(--spacing-large) auto;
  max-width: var(--max-width);
  transform: translateY(-24px);
  outline: 5px solid rebeccapurple;

  .story {
    margin-bottom: 0;
    padding: 0 10px;
  }

  @media (min-width: 1280px) {
    .story {
      width: 100%;
      max-width: 1200px;
      margin: 0 auto;
      padding: 0 10px;
    }
  }

  .story__featured {
    float: left;
  }

  @media (min-width: 1024px) {
    .story__featured {
      position: relative;
      width: 100%;
    }
  }

  .story__featured-card {
    min-height: 540px;
  }

  @media (min-width: 1280px) {
    .story__featured-card {
      width: 100%;
      margin-bottom: 0;
    }
  }

  .story__featured-content {
    margin: 0 25px;
    padding: 32px;
    position: relative;
    top: -24px;
    background-color: #fff;
  }

  @media (min-width: 1024px) {
    .story__featured-content {
      margin: 0;
      width: 576px;
      position: absolute;
    }
  }

  @media (min-width: 1024px) {
    .story__featured-content {
      transform: translateY(100%);
    }
  }

  .story__featured-image {
    width: 100%;
    height: auto;
  }

  @media (min-width: 1024px) {
    .story__featured-image {
      width: 776px;
      height: 517px;
      float: right;
    }
  }

  .story__featured-title {
    font-size: 2.25rem;
    line-height: 1.125;
    margin-bottom: 16px;
  }

  .story__featured-title > .link {
    color: #00598c;
    text-decoration: none;
  }

  .story__featured-blurb {
    margin: 0 0 16px;
    line-height: 1.5;
  }

  .story__featured-source {
    font-weight: bold;
    line-height: 1.25;
  }

  .story__featured-source > .link {
    color: #00598c;
    text-decoration: none;
  }

  .story__featured-source > .link:hover {
    background-color: #0079bf;
    color: #fff;
    text-decoration: none;
  }

  .story__featured-source > .link:active {
    background-color: #003b5c;
    color: #fff;
    text-decoration: none;
  }

  .story__featured-source > .link:focus {
    background-color: #0079bf;
    color: #fff;
    text-decoration: none;
  }

}

[data-layout*="c-stack"] {
  margin-bottom: var(--spacing-large);
  outline: 5px solid dodgerblue;
}

[data-layout*="c-alt"] {
  margin-bottom: var(--spacing-large);
  outline: 5px solid black;
}