.hamburger {
  position: absolute;
  right: -16px;
  top: -50px;
  width: 40px;
  padding: 12px;
  border-radius: 2px;
  bottom: 8px;
 
  &:focus {
    outline: 1px dotted $hover-state;
    outline-offset: -8px;
  }

  &:hover {
    opacity: 1;
    background: $hover-state;

    .hamburger-inner {
      background: $white !important;

      &::before,
      &::after {
        background: $white !important;
      }
    }
  }

  &.is-active {
    &:hover {
      opacity: 1;
    }
  }

  @media (min-width: breakpoint-min(md)) {
    //top: -40px;
    right: 24px;
  }

  @media (min-width: breakpoint-min(lg)) {
    display: none;
  }

